
body {
  background: #283331;
  font-family: 'Open Sans', sans-serif;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  margin-top: 60px;
  /*background: #334034;*/
}
.flex {
  display: -ms-flexbox;
  display: flex;
}
.flex.wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.flex.v-center {
  -ms-flex-align: center;
      align-items: center;
}
.flex.v-bottom {
  -ms-flex-align: end;
      align-items: flex-end;
}
.flex.v-top {
  -ms-flex-align: start;
      align-items: flex-start;
}
.flex.h-center {
  -ms-flex-pack: center;
      justify-content: center;
}
.flex.h-left {
  -ms-flex-pack: left;
      justify-content: left;
}
.flex.h-right {
  -ms-flex-pack: right;
      justify-content: right;
}
.flex.h-space-between {
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.flex.text-wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  word-break: break-all;
}
h1,
h2,
h3 {
  font-family: 'Oswald', sans-serif;
}
.period {
  color: #D66D40;
  font-size: 3em;
}
.logout-bar {
  padding-right: 20px;
}
.linked-text {
  text-decoration: none;
  color: #ACD9AF;
}
.linked-text:hover {
  text-decoration: none;
  color: #6F8C71;
}
.main-title {
  font-size: 4em;
  font-weight: bold;
  letter-spacing: 0.2em;
  color: #F4F4E6;
}



